*{
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}
.main_header{
  width: 100%;
}
.avatar{
  top: 10px;
}
.bars{
  color: white;
  float: right;
  display: none;
  cursor: pointer;
  
}
.check{
  color: white;
  float: right;
  display: none;
 
}
body{
  font-family: montserrat;
}
nav{
  background: #194350;
  height: 65px;
  width: 100%;
  padding: 10px;
}

nav ul{
  float: right;
   margin-right: 20px;
}
nav ul li{
  display: inline-block;
 line-height: 10px;
  margin: 0 5px;
}
nav ul li a{
  color: white;
  font-size: 17px;
  padding: 7px 13px;
  border-radius: 3px;
  text-transform: uppercase;
}



@media (max-width: 450px){
.main_header{
width: 100%;
}
}

