
.banner{
    width: 100%;
   height: 470px;
   background-color: #206a5d;
}
.content{
    margin-left: 50px;
    padding-top:5px ;
    height:190px;
    

}
.pic{
    
    margin-left: 74%;
    border-radius: 40px;
    -webkit-transform: scale(1);
	transform: scale(1);
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
    
}
.pic:hover{
    -webkit-transform: scale(1.1);
   transform: scale(1.1);
   }
.h{
    margin-top: 30px;
    color: rgb(194, 180, 180);
    font-size: 30px;
    margin-left: 25px;
    font-family: sans-serif;
}
.png{
    margin:8px;
    transform: scale(1);
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
    
}

.png:hover{
    background-color:none;
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
    
}
.png_div{
    margin-left: 50px;
}
.banner_p{
    color: rgb(175, 159, 159);
    text-transform: uppercase;
    font-family:serif;
    font-style: italic;
    margin-left: 30px;
}
