@media  (max-width: 420px){
    .banner{
        width: 100%;
       height: 430px;
      
    }
    .content{
        margin-left: 10px;
    padding-top:5px ;
    height:100px;
        
       
       }
       
       .h1{
        margin-top: 0;
        color: rgb(194, 180, 180);
        font-size: 30px;
    }
       .pic{
        margin-left: 180px;
       width: 220px;
       height: 250px;
      
       
    }
    .pic:hover{
    
	transform: scale(1.1);
    }
    .png{
        margin: 2px;
        transform: scale(1);
        -webkit-transition: .3s ease-in-out;
        transition: .3s ease-in-out;
       
    }
    .png:hover{
        background-color: none;
        -webkit-transform: scale(1.3);
        transform: scale(1.3);
        
    }
    .png_div{
        margin-left: 20px;
    }
}
@media  (min-width:420px) and (max-width: 500px){
    .pic{
        margin-left: 20px;
    }
}

@media (min-width:600px) and (max-width:700px){
  .pic{
      margin-left: 190px;
  }
}
@media(min-width:700px) and (max-width:1100px){
    .pic{
        margin-left: 400px;
    }
  }
  @media(min-width:500px) and (max-width:600px){
